	let livePlayer = requirePlugin('live-player-plugin');
	import uniLogin from '@/components/uni-login';
	import pageBottom from '@/components/page-bottom';
	import listCell from '@/components/mix-list-cell';
	let startY = 0, moveY = 0, pageAtTop = true;
    export default {
		components: {
			uniLogin,pageBottom,listCell,
		},
		data(){
			return {
				roomId:0,
				coverTransform: 'translateY(0px)',
				coverTransition: '0s',
				moving: false,
				userInfo:{},
				subscribe: "plugin-private://wx2b03c6e691cd7370/components/subscribe/subscribe",
				storeReport:{},
				storeInfo:{
					id:0
				},
				showLogin:0,
			}
		},
		onShow(){
			// this.livePlayer();
			this.loadData();
		},
		onLoad(){
		},




















        methods: {
			livePlayer(){
				 const roomId = 2 // 房间 id
				    livePlayer.getLiveStatus({ room_id: roomId })
				        .then(res => {
				        // 101: 直播中, 102: 未开始, 103: 已结束, 104: 禁播, 105: 暂停中, 106: 异常，107：已过期 
				            const liveStatus = res.liveStatus
				            console.log('get live status', liveStatus)
				        })
				        .catch(err => {
				            console.log('get live status', err)
				        })
						
			},
			loadData:function(){
				let _this = this;
				setTimeout(()=>{
					uni.request({
						url: _this.$Url + '/clmsj/user/getUser', 
						header:{
							'auth' : uni.getStorageSync('session_key')
						},
						method:'POST',
						success: (res) => {
							if (res.data.code == 0) {
								_this.userInfo = res.data.data.user;
								_this.storeInfo = res.data.data.shop;
								uni.setStorageSync("userInfo",_this.userInfo);
								
								if(_this.userInfo.role == 1){
									uni.request({
										url: _this.$Url + '/clmsj/user/shopSellStatistics', 
										header:{ 'auth' : uni.getStorageSync('session_key') },
										method:'POST',
										success: (res) => {
											if (res.data.code == 0) {
												_this.storeReport = res.data.data;
											}
										},
									})
								}
							}else{
								// uni.navigateTo({
								// 	url : '/pages/public/login'
								// })  
								// return;
							}
						},
					})

				},500);
			},
			getPhoneNumber: function(e) {
				console.log("getPhoneNumber")
				console.log(e)
				let _this = this;
				if (e.detail.errMsg == 'getPhoneNumber:fail user deny') {
					
				} else {
					// that.miniRegisterOrLogin(e.detail.encryptedData,e.detail.iv);
					uni.request({
						url: _this.$Url + '/clmsj/home/getPhoneNumber',
						data: {
							encrypt_data:encodeURIComponent(e.detail.encryptedData),
							iv:e.detail.iv
						},
						header:{
							'auth' : uni.getStorageSync('session_key')
						},
						method:'POST',
						success: (res) => {
							console.log(res)
							if (res.data.code == 0) {
								_this.userInfo.mobile = res.data.mobile;
								_this.$store.commit('login', _this.userInfo);
							}else{
								uni.showToast({
									icon:'none',
									title:'手机号获取失败'
								});
							}
						},
					})
				}
			},
			copy(content){
				uni.setClipboardData({
					data:content,
					success() {
						uni.showToast({
							icon:'none',
							title:'邀请码复制成功'
						});
					}
				})
			},
			navToLive(){
				let roomId = this.roomId;
				livePlayer.getOpenid({ room_id: roomId }) // 该接口传入参数为房间号
					.then(res => {
						console.log('用户进入直播间 get openid', res.openid) // 用户openid
						
						let customParams = encodeURIComponent(JSON.stringify({ path: 'pages/product/product', pid: 1 })) // 开发者在直播间页面路径上携带自定义参数（如示例中的path和pid参数），后续可以在分享卡片链接和跳转至商详页时获取，详见【获取自定义参数】、【直播间到商详页面携带参数】章节（上限600个字符，超过部分会被截断）
						wx.navigateTo({
							url: `plugin-private://wx2b03c6e691cd7370/pages/live-player-plugin?room_id=${roomId}&custom_params=${customParams}`
						})
						
						
					}).catch(err => {
						console.log('get openid', err)
					})
				// path = 'pages/product/product?goods_id=18&sid=1';
			},
			/**
			 * 统一跳转接口,拦截未登录路由
			 * navigator标签现在默认没有转场动画，所以用view
			 */
			navTo(url,type){
				console.log(url)
				if(!uni.getStorageSync('session_key') || !this.userInfo.id){
					// uni.navigateTo({
					// 	url : '/pages/public/login'
					// })  
					this.showLogin = 1;
					this.$refs.uniLogin.toggleMask('loadData');
					return;
				}
				if(type == 'switchTab'){
					uni.switchTab({
						url
					})
				}else{
					uni.navigateTo({  
						url
					})  
				}
			}, 
	
			/**
			 *  会员卡下拉和回弹
			 *  1.关闭bounce避免ios端下拉冲突
			 *  2.由于touchmove事件的缺陷（以前做小程序就遇到，比如20跳到40，h5反而好很多），下拉的时候会有掉帧的感觉
			 *    transition设置0.1秒延迟，让css来过渡这段空窗期
			 *  3.回弹效果可修改曲线值来调整效果，推荐一个好用的bezier生成工具 http://cubic-bezier.com/
			 */
			coverTouchstart(e){
				if(pageAtTop === false){
					return;
				}
				this.coverTransition = 'transform .1s linear';
				startY = e.touches[0].clientY;
			},
			coverTouchmove(e){
				moveY = e.touches[0].clientY;
				let moveDistance = moveY - startY;
				if(moveDistance < 0){
					this.moving = false;
					return;
				}
				this.moving = true;
				if(moveDistance >= 80 && moveDistance < 100){
					moveDistance = 80;
				}
		
				if(moveDistance > 0 && moveDistance <= 80){
					this.coverTransform = `translateY(${moveDistance}px)`;
				}
			},
			coverTouchend(){
				if(this.moving === false){
					return;
				}
				this.moving = false;
				this.coverTransition = 'transform 0.3s cubic-bezier(.21,1.93,.53,.64)';
				this.coverTransform = 'translateY(0px)';
			}
        }  
    }  
